.item{
    background: #c814c8;
    width:70%;
    height:50px;
    margin:20px auto;
    border-radius: 10px;
    position: relative;
}
.item p {
    color:#fff;
    font-size: 22px;
    padding:10px 10px 10px 20px;     
    position: relative;
}
.img{
    position: absolute;
    width: 30px;
    height: 30px;
    right: 10px;;
    cursor: pointer;

   
}