body{
  background:#0066cc; 
}
.App{
  background: #142952;
  min-height: 500px;
  width:500px;
  margin:50px auto;
  padding-bottom: 10px;
}
.App input{
  background:#2f9f9f;
  height: 50px;
  width:200px;
  padding:0px 20px;
  margin: 20px 0px 20px 60px;
  font-size:22px;
  color:#fff;
  border:none;
  border-radius: 5px;
}
.App input:focus{
 outline:none;
}
.App input::placeholder{
  color: #fff;
  font-size:22px;
}
.App button{
  background:#e9a40e;
  margin-left:40px;
  padding:10px 15px;
  color:#fff;
  font-size:20px;
  font-weight: bold;
  border:none;
  cursor: pointer;
  border-radius: 5px;
}
.App button:focus{
  outline:none;
 }